import React from "react";
import {
  ContentfulRichTextGatsbyReference,
  RenderRichTextData,
} from "gatsby-source-contentful/rich-text";
import Img, { FluidObject } from "gatsby-image";
import "./ServiceBlock.scss";
import { RenderRichContent } from "./RenderRichContent";
type ServiceBlockProps = {
  heading?: string;
  services: {
    image: { fluid: FluidObject };
    title: string;
    description: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  }[];
};
// Height of feature image should be 400px * mandatory => Use graphql for img height ?h=400
export const ServiceBlock = ({ heading, services }: ServiceBlockProps) => {
  return (
    <div className="expectations">
      <div className="heading text-center">{heading}</div>
      <div className="services">
        {services.map((service, index) => {
          return (
            <div className="service" key={index}>
              <Img
                className="image"
                fluid={service.image.fluid}
                loading="lazy"
                alt="content"
              />
              <div className="title">{service.title}</div>
              <RenderRichContent
                content={service.description}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
};
