import React from "react";
import { RenderRichContent } from "./RenderRichContent";
import Img from "gatsby-image";
import "./ImageBlock.scss";
type ImageBlockProps = {
  align?: "left" | "right";
  content: any;
};
// Height of feature image should be 400px * mandatory => Use graphql for img height ?h=400
export const ImageBlock = ({ align, content }: ImageBlockProps) => {
  console.log(content.heading1);

  return (
    <div className="image-block-wrapper" img-align={align}>
      <Img
        className="image"
        fluid={content.image1.fluid}
        loading="lazy"
        alt="content"
      />
      <div className="text">
        <div className="text__wrapper">
          <RenderRichContent content={content.heading1} />
          {content.heading2 ? (
            <>
              <img
                className="image-icon"
                src={content.image2.file.url + "?w=40"}
                alt="heading icon"
              />
              &nbsp;
              <RenderRichContent
                content={content.heading2}
                className="heading2 inline-block"
              />
            </>
          ) : (
            <></>
          )}
          <RenderRichContent content={content.description} />
          {content.button ? (
            <button className="button">{content.button}</button>
          ) : (
            <></>
          )}
        </div>
      </div>
    </div>
  );
};
